<template>
  <div id="email-page">
    <div class="contact-email">
        <div class="text">Email User</div>
        <form class="contact-form" id="contact-form" @submit.prevent="sendEmail(nameString, emailString, subjectString, messageString)">
            <div class="field-name-email">
                <input class="field-name" v-model="nameString" type="text" placeholder="Name" required>
                               
                <input class="field-email" v-model="emailString" type="email" placeholder="Email" required>
            </div>
            <div class="field-subject">
                           
                <input class="field-subject-val" v-model="subjectString" type="text" placeholder="Subject" required>
                           
            </div>
            <div class="field-message">
                <textarea class="field-message-val" v-model="messageString" rows="10" cols="30" placeholder="Message..." required></textarea>
            </div>
            <div class="button">
                <button class="btn" type="submit">Send</button>
            </div>
        </form>
    </div>
  </div>
</template>

<script>

import emailjs from 'emailjs-com';


export default {
  name: 'Email',
  components: {
    
  },
  data() {
    return {
      
    }
  },

  methods:{
    sendEmail(name, email, subject, message) {
        var templateParams = {
            nameString: name,
            emailString: email,
            subjectString: subject,
            messageString: message
        };
        emailjs.send('service_7l1bd06', 'template_alyxy3k', templateParams, 'user_pHLbTedmJtXTMpEkajwYK')
            .then((result) => {
                console.log('SUCCESS!', result.status, result.text);
                alert('Sent!');
            }, (error) => {
                console.log('FAILED...', error);
                alert(JSON.stringify(error));
            });
        document.getElementsByClassName("field-name")[0].value = "";
        document.getElementsByClassName("field-email")[0].value = "";
        document.getElementsByClassName("field-subject-val")[0].value = "";
        document.getElementsByClassName("field-message-val")[0].value = "";
    },
  }
}
</script>

<style>
#email-page {
margin: 0;
padding:0;


}

.contact-email{
    max-width: 1024px;
    height: 100kvh;
    align-items: center;
    align-self: center;
    margin: auto;
}

.text{
    text-align: start;
    color:black;
    font-weight: 400;
    font-size: 25px;
    font-style: italic;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    
}

.field-name-email{
    display: flex;
    width: 100%;
    padding: 20px 0;
    
}

.field-name{
    flex:1;
    margin-right: 10px;
}

.field-email{
    flex:1;
    margin-left: 10px;
}

.field-subject-val{
    display: flex;
    padding: 20px 0px;
    width: 100%;
    margin-bottom: 20px;
}

.field-message-val{
    display: block;
    width: 100%;
    
}

textarea{
    float: left;
    width: 100%;
    margin-top: 20px;
}

.form .button{
    display: flex;
    padding: 20px 40px;
    align-items: center;
}

input{
    height: 100%;
    width: 100%;
    border: 1px solid lightgrey;
    border-radius: 6px;
    outline: none; 
    font-size: 17px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

button{
    width: 100%;
    height: 100%;
    border: 2px solid crimson;
    border-radius: 6px;
    padding: 10px 30px;
    margin-top: 20px;
    color: white;
    background:crimson;
    font-size: 20px;
    font-weight: 500;
}

button:hover{
    background:none;
    color:crimson;
}
    
</style>
