<template>
  <div id="admin-page">
    <button class="add-button" @click="openAddForm">Add User</button>

    <div class="formAdd-popup" id="myAddForm">
      <form action="/action_page.php" class="formAdd-container">
        <h1>ADD USER</h1>
        
        <label for="email"><b>Email</b></label>
        <input type="email" v-model="emailString" placeholder="Enter Email" name="email" required>

        <label for="password"><b>Password</b></label>
        <input type="text" v-model="passwordString" placeholder="Enter Password" name="password" required>

        <button type="submit" class="btn" @click="addUser(emailString, passwordString)">Add</button>
        <button type="button" class="btn cancel" @click="closeAddForm">Close</button>
      </form>
    </div>

    <div class="formUpdate-popup" id="myUpdateForm">
      <form action="/action_page.php" class="formUpdate-container">
        <h1>UPDATE USER</h1>

        <label for="email"><b>Email</b></label>
        <input type="email" v-model="emailUpdate" placeholder="Enter Email" name="email" required>

        <label for="password"><b>Password</b></label>
        <input type="text" v-model="passwordUpdate" placeholder="Enter Password" name="password" required>

        <label for="role"><b>Role</b></label>
        <input type="text" v-model="roleUpdate" placeholder="Enter Role" name="role" required>

        <button type="submit" class="btn" @click="updateUser(emailUpdate, passwordUpdate, roleUpdate)">Update</button>
        <button type="button" class="btn cancel" @click="closeUpdateForm">Close</button>
      </form>
    </div>

    <!-- <AddUser v-bind:users="users" v-on:add-user="addUser"/> -->
    <!-- <div class="div-user">
      <button class="AddUser">Add User</button>
    </div> -->
    <div class="Main-display">
     <div class="Users-display"><user :users="users" v-on:open-modify="openModify" v-on:del-user="delUser"/></div>
    </div>
    
    
  </div>
</template>

<script>

//import AddUser from '../components/AddUser'
import User from '../components/User.vue'
import axios from 'axios'

export default {
  name: 'Admin',
  components: {
    //AddUser,
    User,
  },
  data() {
    return {
      id : 0,
      failed : false,
      users : [
        /*{
            id : 1,
            email : "kimperez@yahoo.com",
            password : "kimclaude",
            role : "Admin",
            Active : 1
        },
        {
            id : 2,
            email : "claude@gmail.com",
            password : "12345",
            role : "User",
            Active : 1
        }*/
      ]
    }
  },

  methods:{
    openAddForm() {
      document.getElementById("myAddForm").style.display = "block";
    },

    closeAddForm() {
      document.getElementById("myAddForm").style.display = "none";
    },

    openModify(id){
      document.getElementById("myUpdateForm").style.display = "block";
      this.id = id;
      
    },

    closeUpdateForm() {
      document.getElementById("myUpdateForm").style.display = "none";
    },

    updateUser(emailUpdate, passwordUpdate, roleUpdate){
        /*for(let i =0; i<this.users.length; i++){
           if(this.id != i){
             if(emailUpdate == this.users[i].email){
               alert('Email already existing!');
               this.failed = true;
             }
           }
           else{
             //some code
          }
         }*/

      //e.preventDefault();
      if(this.failed == false){
        if(emailUpdate == "None" || emailUpdate == null){
            alert('Please Enter Valid Users!');
        } 
        else {
          const updateUser = {
            id : this.id, 
            email: emailUpdate,
            password: passwordUpdate,
            role: roleUpdate
          }

          const {id, email, password, role} = updateUser;

          axios.put('http://localhost:5000/api/users', {
            id,
            email,
            password,
            role
          })
          .then(res => this.users = res.data)
          .catch(error =>{
            return Promise.reject(error);
          })
          alert('Updated Successfully');
          document.location.reload();
        }
      }
    },

    delUser(id){
      var answer = window.confirm("Are you sure to delete this data?");
      if (answer) {
          //some code
        axios.delete(`http://localhost:5000/api/users/${id}`)

        .then(res => {
          this.users = this.users.filter(user => user.id !== id)
          return res
        })
        .catch(error =>{
          return Promise.reject(error);
        })
      }
      else {
          //some code
      }
      
      //this.users = this.users.filter(user => user.id !== id);
    },
    addUser(emailString, passwordString){
      for(let i =0; i<this.users.length; i++){
        if(emailString == this.users[i].email){
          alert('Email already existing!');
          this.failed = true;
        }
      }

      //e.preventDefault();
      if(this.failed == false){
        if(emailString == "None" || emailString == null){
            alert('Please Enter Valid Users!');
        } 
        else {
          const newUser = {
            //id : this.id, 
            email: emailString,
            password: passwordString
          }

          const {email, password} = newUser;

          axios.post('http://localhost:5000/api/users', {
            email,
            password
          })
          .then(res => this.users = [...this.users, res.data])
          .catch(error =>{
            return Promise.reject(error);
          })
              //this.users = [...this.users, newUser];
            alert('Added Successfully');
            document.location.reload();
          //this.$emit('add-value', newValue);
          //alert('Successfully Stored!!');
        }
      }
    }
  },
  created(){
      /* eslint-disable no-debugger */
      axios.get('http://localhost:5000/api/users', {mode:'cors'})
        
      .then( res => this.users = res.data)
      
      .catch(error =>{
        return Promise.reject(error);
      })
  },
}
</script>

<style>
#admin-page {
margin: 0;
padding:0;
background:navy;
}



.Main-display{
  /*height: 100vh; 
  color: navy;*/
  align-items: center;
}

.add-button {
  background-color: #555;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  position: fixed;
  bottom: 23px;
  right: 28px;
  width: 280px;
}

/* The popup form - hidden by default */
.formAdd-popup {
  display: none;
  position: fixed;
  bottom: 0;
  right: 15px;
  border: 3px solid #f1f1f1;
  z-index: 9;
}

/* Add styles to the form container */
.formAdd-container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* Full-width input fields */
.formAdd-container input[type=text], .formAdd-container input[type=email] {
  width: 85%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
}

/* When the inputs get focus, do something */
.formAdd-container input[type=text]:focus, .formAdd-container input[type=email]:focus {
  background-color: #ddd;
  outline: none;
}

/* Set a style for the submit/login button */
.formAdd-container .btn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom:10px;
  opacity: 0.8;
}

/* Add a red background color to the cancel button */
.formAdd-container .cancel {
  background-color: red;
}

/* Add some hover effects to buttons */
.formAdd-container .btn:hover, .open-button:hover {
  opacity: 1;
}


/*---------------------------------------------*/

/* The popup form - hidden by default */
.formUpdate-popup {
  display: none;
  position: fixed;
  bottom: 0;
  right: 15px;
  border: 3px solid #f1f1f1;
  z-index: 9;
}

/* Add styles to the form container */
.formUpdate-container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* Full-width input fields */
.formUpdate-container input[type=text], .formUpdate-container input[type=email] {
  width: 85%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
}

/* When the inputs get focus, do something */
.formUpdate-container input[type=text]:focus, .formUpdate-container input[type=email]:focus {
  background-color: #ddd;
  outline: none;
}

/* Set a style for the submit/login button */
.formUpdate-container .btn {
  background-color: #04AA6D;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom:10px;
  opacity: 0.8;
}

/* Add a red background color to the cancel button */
.formUpdate-container .cancel {
  background-color: red;
}

/* Add some hover effects to buttons */
.formUpdate-container .btn:hover, .open-button:hover {
  opacity: 1;
}

</style>
